import { LayoutType } from '@/layouts'
import Stores from '@/apps/stores/views/stores.vue'
import Store from '@/apps/stores/views/store.vue'
import CreateStore from '@/apps/stores/views/create-store.vue'

export function StoresApp ({ router }) {
  router.addRoute({
    path: '/stores/',
    name: 'stores',
    component: Stores,
    meta: {
      layout: LayoutType.BASIC
    }
  })

  router.addRoute('stores', {
    path: 'new',
    name: 'create-store',
    component: CreateStore
  })

  router.addRoute('stores', {
    path: ':guid',
    name: 'store',
    component: Store,
    props: true
  })
}
