<template>
  <div class="promotion">
    <transition name="fade">
      <!-- State: Idle -->
      <div
        v-if="isIdle"
        class="promotion__details"
      >
        <generic-details />
      </div>

      <!-- State: Initializing -->
      <div
        v-if="isInitializing"
        class="promotion__state"
      >
        <loading-indicator is-loading />
      </div>

      <!-- State: Error -->
      <div
        v-else-if="hasError"
        class="promotion__state"
      >
        <p>{{ errorMessage }}</p>
      </div>
    </transition>
  </div>
</template>

<script>
import GenericDetails from '@/components/generic/details-view/generic-details'
import { createNamespacedHelpers } from 'vuex'
import { promotionService } from '@/services'
import LoadingIndicator from '@/components/widgets/loading-indicator'

const detailsStore = createNamespacedHelpers('details')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_NOT_FOUND: 'errorNotFound',
  ERROR_UNAUTHORIZED: 'errorUnauthorized',
  ERROR_UNKNOWN: 'errorUnknown'
}

export default {
  name: 'Promotion',
  components: {
    GenericDetails,
    LoadingIndicator
  },
  props: {
    contentLanguage: {
      type: String,
      required: true
    },
    campaignGuid: {
      type: String,
      required: true
    },
    promotionGuid: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      state: ComponentState.INITIALIZING
    }
  },
  computed: {
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR_NOT_FOUND ||
             this.state === ComponentState.ERROR_UNAUTHORIZED ||
             this.state === ComponentState.ERROR_UNKNOWN
    },
    errorMessage () {
      const errorMessages = {
        [ComponentState.ERROR_NOT_FOUND]: this.$t('editPromotion.promotionNotFoundError'),
        [ComponentState.ERROR_UNAUTHORIZED]: this.$t('editPromotion.promotionUnauthorizedError'),
        [ComponentState.ERROR_UNKNOWN]: this.$t('editPromotion.promotionUnknownError')
      }

      return errorMessages[this.state] || ''
    }

  },
  async created () {
    try {
      const entity = (await promotionService.fetchEntityAsync({ guid: this.promotionGuid, cultureCode: this.contentLanguage })).data
      this.setDetails({ details: entity })
      this.state = ComponentState.IDLE
    } catch (e) {
      if (e.response && e.response.status) {
        switch (e.response.status) {
          case 404:
            this.state = ComponentState.ERROR_NOT_FOUND
            break
          case 401:
            this.state = ComponentState.ERROR_UNAUTHORIZED
            break
          default:
            this.state = ComponentState.ERROR_UNKNOWN
        }
      } else {
        throw e
      }
    }
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails'])
  }
}
</script>
<style lang="stylus" scoped>
.promotion__state
  position fixed
  top 0
  left 0
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  height 100vh
</style>
