import { LayoutType } from '@/layouts'
import EditPromotion from './views/edit-promotion.vue'
import CreatePromotion from './views/create-promotion.vue'

export function PromotionsApp ({ router }) {
  router.addRoute({
    path: '/campaigns/:campaignGuid/promotions/new',
    name: 'create-promotion',
    component: CreatePromotion,
    props: true,
    meta: {
      layout: LayoutType.BASIC
    }
  })

  router.addRoute({
    path: '/campaigns/:campaignGuid/promotions/:promotionGuid',
    name: 'edit-promotion',
    component: EditPromotion,
    props: true,
    meta: {
      layout: LayoutType.BASIC,
      key: 'promotionGuid'
    }
  })
}
